.about-me {
  box-sizing: border-box;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  &__photo {
    display: block;
    position: absolute;
    bottom:0;
    left:-20px;
    width: 675px;
    height: 616px;
  }
  &__page-title {
    position: absolute;
    top: 20px;
    right: 200px;
    font-family: 'Norse', 'sans-serif';
    font-size: 120px;
    font-weight: 700;
    color: rgba(52, 195, 161, 1)
  }
  &__text {
    & > p {
      text-indent: 30px;
    }
    display: block;
    position: absolute;
    bottom: 100px;
    right: -5px;
    border: 5px solid #F7941D;
    font-family: 'Roboto', 'sans-serif';
    font-size: 24px;
    color: white;
    width: 1000px;
    text-align: justify;
    padding: 10px;
  }
  &__icons-group {
    position: absolute;
    bottom: 20px;
    right: 200px;
    display: flex;
    height: fit-content;
    .about-me__icon {
      width: 64px;
      height: 64px;
      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
}