.exp {
  box-sizing: border-box;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .toggler-wrapper {
      position: relative;
      display: flex;
      width: 50%;
      perspective: 1000px;
      background: center url('../../Icons/MainLogo.svg') no-repeat;
    .toggler {
      width: 100%;
      position: absolute;
      transform: translateZ(-200px);
      transform-style: preserve-3d;
      transition: transform 1s;
      &--programmer-active {
        transform: translateZ(-200px) rotateY(0deg);
      }
      &--sushimaker-active {
        transform: translateZ(-200px) rotateY(180deg);
      }
      .photo__programmer {
        position: absolute;
        left: 50px;
        transform: rotateY(0deg) translateZ(200px);
        filter: blur(10px);
        transition: filter 1s;
      }
      .photo__sushimaker {
        position: absolute;
        left: 50px;
        width: 468px;
        height: 740px;
        transform: rotateY(180deg) translateZ(200px);
        filter: blur(20px);
        transition: filter 1s;
      }
      .current-job-photo {
        filter: blur(0);
      }
    }
  }
  
  .exp-desc {
    width: 50%;
    .exp-page-title {
      position: absolute;
      top: 50px;
      right: 200px;
      font-family: 'Norse', 'sans-serif';
      font-size: 120px;
      font-weight: 700;
      color: rgba(52, 195, 161, 1)
    }
    .job-detail {
      position: absolute;
      bottom: 20px;
      right: 20px;
      width: 650px;
      height: 460px;
      color: white;
      font-family: 'Roboto', 'sans-serif';
      &__title {
        position: absolute;
        top: -32px;
        left: 50px;
        font-family: 'Norse', 'sans-serif';
        font-size: 64px;
        font-weight: 700;
        height: 10%;
        color: #F7941D;
        &::before {
          display: inline-block;
          position: absolute;
          top:32px;
          left: -50px;
          width: 30px;
          height: 42px;
          content: '';
          border-left: 5px solid #F7941D;
          border-top: 5px solid #F7941D;
        }
      }
      &__icon {
        position: absolute;
        top: -40px;
        right: 10px;
        width: 128px;
        height: 128px;
      }
      &__period {
        display: flex;
        align-items: center;
        position: absolute;
        box-sizing: border-box;
        top:10%;
        width: 70%;
        height: 10%;
        border-top: 5px solid #F7941D;
        border-left: 5px solid #F7941D;
        padding: 0 10px;
        &::before {
          display: inline-block;
          position: absolute;
          top:-5px;
          left: 10px;
          width: 100%;
          height: 48px;
          content: '';
          border-right: 5px solid #F7941D;
          transform: skewX(30deg);
        }
        &--prop {
          display: inline-block;
          font-size: 32px;
          font-weight: 700;
          margin-right: 20px;
        }
        &--value {
          font-size: 18px;
        }
      }
      &__place {
        position: absolute;
        top:20%;
        width: 100%;
        height: 30%;
        border-top: 5px solid #F7941D;
        border-left: 5px solid #F7941D;
        border-right: 5px solid #F7941D;
        padding: 0 10px;
        &--prop {
          display: inline-block;
          font-size: 32px;
          font-weight: 700;
          margin-right: 20px;
        }
        &--value {
          font-size: 18px;
        }
      }
      &__responsibilities {
        position: absolute;
        top:30%;
        width: 100%;
        height: 70%;
        border: 5px solid #F7941D;
        padding: 0 10px;
        &--prop {
          display: inline-block;
          font-size: 32px;
          font-weight: 700;
          margin-right: 20px;
        }
        &--value {
          display: block;
          text-align: justify;
          font-size: 24px;
        }
      }
    }
  }
}