.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  padding-bottom: 5px;
  &__right-icons-wrapper {
    display: flex;
    .header__logo-part1 {

      animation: logoRotateLeft 60s linear infinite;
    }
    .header__logo-part2 {
      position: absolute;
      top: -15px;
      left: -14px;
      animation: logoRotateRight 50s linear infinite;
    }

    .social-link {
      cursor: pointer;
        margin-right: 20px;
      &:hover{
        filter: brightness(1.2);
      }
      &__icon {
        width: 60px;
        height: 60px;
      }
    }
  }
  &__left-icons-wrapper {
    .social-icon {
      margin-right: 10px;
    }
  }
}
.icon-text-pair {
  display: flex;
  align-items: center;
  color: white;
  font-family: 'Roboto', 'sans-serif';
  font-size: 32px;
  margin-right: 20px;
  &:first-child {
    margin-right: 20px;
  }
  &__text {
    margin-left: 10px;
  }

}
@keyframes logoRotateRight {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes logoRotateLeft {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}