@import './skills-animations.scss';
.skills {
  box-sizing: border-box;
  display: flex;
  position: absolute;
  top: 0;
  left: 20px;
  width: 100%;
  height: 100%;
  .circle-tech--outer {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    position: absolute;
    top: 20px;
    left: 0;
    width: 650px;
    height: 650px;
    border: 5px solid #F7941D;
    border-radius: 50%;
    background: black;
    & > .skills-list {
      display: flex;
      position: absolute;
      width: 100%;
      left: 10px;
      height: 100%;
      color: white;
      font-size: 24px;
      font-family: 'Nordic', 'sans-serif';
      transform: rotateZ(-90deg);
      & span {
        display: block;
        position: absolute;
        left: 50%;
        //top: 10px;
        transform-origin: 0 310px;
      }
    }
    & > .skills-title {
      position: absolute;
      bottom: 29px;
      right: 174px;
      color: white;
      font-size: 24px;
      font-family: 'Roboto', 'sans-serif';
      transform: rotateZ(156deg);
      cursor: pointer;
      &--active {
        span {
          animation: skillsTitleActive linear 2s forwards;
        }
      }
      &--passive {
        animation: skillsTitlePassive linear 2s forwards;
      }
      & span {
        position: absolute;
        left: 50%;
        top: 10px;
        transform-origin: 10px 320px;
      }
    }
  }
  .circle-tech--inner {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    position: absolute;
    top: 70px;
    left: 50px;
    width: 550px;
    height: 550px;
    border: 5px solid #F7941D;
    border-radius: 50%;
    & > .skills-list {
      display: flex;
      position: absolute;
      width: 100%;
      left: -10px;
      height: 100%;
      color: white;
      font-size: 24px;
      font-family: 'Nordic', 'sans-serif';
      transform: rotateZ(110deg);
      & span {
        display: block;
        position: absolute;
        left: 50%;
        transform-origin: 0 260px;
      }
    }
    & > .skills-title {
      display: block;
      position: absolute;
      left: 160px;
      top: 16px;
      width: fit-content;
      height: fit-content;
      color: white;
      font-size: 24px;
      font-family: 'Roboto', 'sans-serif';
      transform: rotateZ(-22deg);
      cursor: pointer;
      &--active {
        span {
          animation: skillsTitleActive linear 2s forwards;
        }
      }
      &--passive {
        span {
          animation: skillsTitlePassive linear 2s forwards;
        }
      }
      & span {
        position: absolute;
        left: 50%;
        top: 10px;
        transform-origin: 10px 260px;
        font-weight: 700;
        font-size: 32px;
        color: #F7941D;
      }
    }
  }
  
  .circle-lang {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    position: absolute;
    width: 450px;
    height: 450px;
    border: 5px solid #F7941D;
    border-radius: 50%;
    background: center no-repeat url('../../Icons/bg-logo.svg');
    background-size: 480px;
    @mixin circle-logo {
      position: absolute;
      
      width: 100px;
      height: 100px;
    }
    .circle-logo--active {
      animation: circleLogoActive linear 2s forwards;
    }
    .circle-logo--passive {
      animation: circleLogoPassive linear 2s forwards;
    }
    .tech-skill-logo {
      @include circle-logo;
      top: 10px;
    }
    .lang-skill-logo {
      @include circle-logo;
      bottom: 10px;
      transform: scale(0.8); 
    }
  }
  .skill-desc {
    position: absolute;
    overflow-y: auto;
    top: 240px;
    left: 200px;
    max-width: 1300px;
    text-align: justify;
    max-height: 210px;
    min-height: 210px;
    padding: 20px;
    border: 5px solid #F7941D;
    background: black;
    & > p {
      font-family: 'Roboto', 'sans-serif';
      font-size: 32px;
      font-weight: 400;
      color: white;
    }
  }
  .page-title {
    position: absolute;
    top: 50px;
    right: 200px;
    font-family: 'Norse', 'sans-serif';
    font-size: 170px;
    font-weight: 700;
    color: rgba(52, 195, 161, 1)
  }
}
.big-circle--active {
  animation: bigCircleActive ease-in-out 2s forwards;  
}
.big-circle--passive {
  animation: bigCirclePassive ease-in-out 2s forwards;  
}
.small-circle--active {
  animation:  smallCircleActive ease-in-out 2s forwards;
}
.small-circle--passive {
  animation: smallCirclePassive ease-in-out 2s forwards;
}
