@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import './fonts/fonts.scss';
@import './components/Main/main.scss';
@import './components/Header/header.scss';
@import './components/Footer/footer.scss';

$border-weight: 5px;
*, html, body {
  margin: 0;
  padding:0;
  box-sizing: border-box;
}
.background {
  display: flex;
  justify-content: center;
  position: absolute;
  margin: 0;
  padding:0;
  width: 100vw;
  min-width: 1366px;
  height: 100vh;
  box-sizing: border-box;
  background: #0B0B0B;
  z-index: -10;
  padding: 20px;
  overflow: hidden;
}
.container {
  display: flex;
  flex-direction: column;
  position: relative;
  box-sizing: border-box;
  padding: 5px;
  width: 1366px;
  height: 100%;
  background: #0B0B0B;
  border: $border-weight solid transparent;
  border-radius: 10px;
  background-clip: padding-box;
  &::after {
    position: absolute;
    top: -$border-weight;
    bottom: -$border-weight;
    left: -$border-weight;
    right: -$border-weight;
    background: linear-gradient(to bottom, rgba(52, 195, 161, 1), rgba(55, 182, 151, 0));
    content: '';
    z-index: -1;
    border-radius: 10px;
  }
  .main-wrapper {
    position: relative;
    width: 100%;
    height: 720px;
