@font-face {
  font-family: 'Nordic';
  src: url('./nordic/Nordic.eot');
  src: url('./nordic/Nordic.eot?#iefix') format('embedded-opentype'),
       url('./nordic/Nordic.woff2') format('woff2'),
       url('./nordic/Nordic.woff') format('woff'),
       url('./nordic/Nordic.ttf')  format('truetype'),
}
@font-face {
  font-family: 'Norse';
  src: url('./norse/Norse.eot');
  src: url('./norse/Norse.eot?#iefix') format('embedded-opentype'),
       url('./norse/Norse.woff2') format('woff2'),
       url('./norse/Norse.woff') format('woff'),
       url('./norse/Norse.ttf')  format('truetype'),
}