.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  //height: 100%;
  min-height: max-content;
  .navigation {
    //background: red;
    display: flex;
    padding: 0 40px;
    //justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 62px;
    max-width: inherit;
    font-family: 'Nordic', 'sans-serif';
    font-size: 32px;
    font-weight: 400;
    list-style: none;
    border-top: 2px solid rgba(52, 195, 161, 1);
    border-bottom: 2px solid rgba(52, 195, 161, 1);
    color: white;
    &__item {
      width: 25%;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      padding: 0 20px;
      border-left: 2px solid rgba(52, 195, 161, 1);
      &:last-child {
        border-right: 2px solid rgba(52, 195, 161, 1);
      }
      transform: skewX(-30deg);
      & > a {
        position: absolute;
        transform: skewX(30deg);
        text-decoration: none;
        color: inherit;
        transition: font-size .5s;
      }
      .active {
        font-size: 38px;
        color: rgba(52, 195, 161, 1);
        text-shadow: 2px 0px 4px rgba(52, 195, 161, .8);
      }
    }    
  }
}
.right-nav-icon {
  position: absolute;
  color: linear-gradient(to bottom, rgba(52, 195, 161, 1), rgba(55, 182, 151, 0.8));
}