.home {
  box-sizing: border-box;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .me-img-wrapper {
    &::before {
      position: absolute;
      content: '';
      width: 50%;
      height: 100%;
      top:0;
      left: 0;
      background: radial-gradient(circle, rgba(247,148,29,0) 8%, rgba(247,148,29,0.6643032212885154) 10%, rgba(247,148,29,0) 72%, rgba(247,148,29,0) 100%);
    }
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 50%;
    .home__me-photo {
      display: block;
      width: 537.6px;
      height: 681.6px;
      z-index: 1;
    }
  }
  .main-title-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 50%;
    padding: 20px;
    .home__main-logo {
      width: 500px;
      height: 500px;
    }
  }
  .home__profession {
    font-family: 'Nordic', 'sans-serif';
    font-size: 32px;
    font-weight: 700;
    color: rgba(52, 195, 161, .9) ;
  }
  .home__name {
    font-family: 'Norse', 'sans-serif';
    font-size: 72px;
    font-weight: 700;
    color: #f7941d;
  }
}