@keyframes bigCircleActive {
  0% { transform: rotate(180deg); }
  10% { transform: rotate(190deg); }
  90% { transform: rotate(-10deg); }
  100% { transform: rotate(0deg); }
}
@keyframes bigCirclePassive {
  0% { transform: rotate(0deg); }
  10% { transform: rotate(-10deg); }
  90% { transform: rotate(190deg); }
  100% { transform: rotate(180deg); }
}
@keyframes smallCircleActive {
  0% { transform: rotate(0deg); }
  10% { transform: rotate(10deg); }
  90% { transform: rotate(-190deg); }
  100% { transform: rotate(-180deg); }
}
@keyframes smallCirclePassive {
  0% { transform: rotate(-180deg); }
  10% { transform: rotate(-190deg); }
  90% { transform: rotate(10deg); }
  100% { transform: rotate(0deg); }
}
@keyframes circleLogoActive {
  0% { transform: scale(0.8); }
  100% { transform: scale(1); }
}
@keyframes circleLogoPassive {
  0% { transform: scale(1); }
  100% { transform: scale(0.8); }
}
@keyframes skillsTitleActive {
  0% { font-size: 24px; font-weight: 400; color: white; }
  100% { font-size: 32px; font-weight: 700; color: #F7941D; }
}
@keyframes skillsTitlePassive {
  0% { font-size: 32px; font-weight: 700; color: #F7941D; }
  100% { font-size: 24px; font-weight: 400; color: white; }
}